import React, { Component } from 'react'
import { Link } from 'react-router'
import Container from '../../elements/Container'
import { Dropdown, DropdownItem, DropdownItemSep } from '../../elements/Dropdown'
import CSSModules from 'react-css-modules'
import Logo from '../Logo'
const styles = require('./ConsoleBranding.scss')

interface Props {
  username: string
  isAdmin?: boolean
  onLogout: () => void
}

interface State {
  isDropdownMenuVisible: boolean
}

@CSSModules(styles, {
  allowMultiple: true
})
class ConsoleBranding extends Component<Props, State> {

  constructor(props) {
    super(props)
    this.state = {
      isDropdownMenuVisible: false
    }
    this.handleLogoutClick = this.handleLogoutClick.bind(this)
  }

  handleLogoutClick(e) {
    this.props.onLogout()
    e.preventDefault()
  }

  toggleDropdownMenu() {
    this.setState({
      isDropdownMenuVisible: !this.state.isDropdownMenuVisible
    })
  }

  render() {
    let username = this.props.username
    let isAdmin = this.props.isAdmin ? this.props.isAdmin : 'false'

    return (
      <div styleName="branding">
        <Container isFluid={true} className="clearfix">
          <div>
            <div className="left">
              <Logo to="/console" />
            </div>
            <div styleName="sub-name">
              <span styleName="sub"></span>
            </div>
            <div styleName="nav right">
              {
                username && (
                  <Dropdown
                    style="dark"
                    title={(
                      <span>{username}{ isAdmin && <span className="badge badge--dark"></span> }</span>
                    ) }
                    >
                    <DropdownItem><Link to="/"> Readr</Link></DropdownItem>
                    <DropdownItem><Link to={`/user/profile`}></Link></DropdownItem>
                    <DropdownItem><Link to={`/user/preference`}></Link></DropdownItem>
                    <DropdownItemSep />
                    <DropdownItem><a onClick={this.handleLogoutClick} href="#"></a></DropdownItem>
                  </Dropdown>
                )
              }
            </div>
          </div>
        </Container>
      </div>
    )
  }
}

export default ConsoleBranding



