import React, { Component } from 'react'
import { Container } from '../../elements/layout'
import { Dropdown, DropdownItem, DropdownItemSep } from '../../elements/Dropdown'
import Logo from '../Logo'
import CSSModules from 'react-css-modules'
import { Link } from 'react-router'
const styles = require('./Branding.scss')

interface IProps {
  username: string
  isAdmin?: boolean
  className?: string
  recentReading?: {
    title: string
    id: string
  }[]
  onLogout: () => void
}

interface IState {
  isDropdownMenuVisible: boolean
}

@CSSModules(styles, {
  allowMultiple: true
})
class Branding extends Component<IProps, IState> {

  constructor(props) {
    super(props)
    this.state = {
      isDropdownMenuVisible: false
    }
    this.handleLogoutClick = this.handleLogoutClick.bind(this)
  }

  toggleDropdownMenu() {
    this.setState({
      isDropdownMenuVisible: !this.state.isDropdownMenuVisible
    })
  }

  handleLogoutClick(e) {
    this.props.onLogout()
    e.preventDefault()
  }

  render() {
    let isAdmin = this.props.isAdmin ? this.props.isAdmin : false

    const { username, recentReading } = this.props

    return (
      <div styleName={`branding ${this.props.className ? this.props.className : ''}`}>
        <Container className="clearfix">
          <div>
            <div className="left">
              <Logo/>
            </div>
            <div className="left" styleName="nav">
              <ul styleName="nav-links">
                <li styleName="nav-item">
                  <Link styleName="nav-link" to="/browse"></Link>
                </li>
              </ul>
            </div>
            {
              username
                ? (
                  <div styleName="nav--user">
                    {
                      recentReading.length !== 0 && (
                        <Dropdown styleName="dropdown-recent-reading" title="">
                          {
                            recentReading.slice(0, 5).map((book, index) => {
                              return (
                                <DropdownItem key={index}>
                                  <Link to={`/viewer/book/${book.id}`}>{book.title}</Link>
                                </DropdownItem>
                              )
                            })
                          }
                          <DropdownItemSep />
                          <DropdownItem><Link to={`/user/shelf`}></Link></DropdownItem>
                        </Dropdown>
                      )
                    }
                    <Dropdown
                      title={(
                        <div style={{ display: 'inline-block' }}>{username}{ isAdmin && <span className="badge"></span> }</div>
                      ) }
                      >
                      {
                        isAdmin === true && (
                          <DropdownItem><Link to="/console"></Link></DropdownItem>
                        )
                      }
                      <DropdownItem><Link to={`/user/shelf`}></Link></DropdownItem>
                      <DropdownItem><Link to={`/user/profile`}></Link></DropdownItem>
                      <DropdownItem><Link to={`/user/preference`}></Link></DropdownItem>
                      <DropdownItemSep />
                      <DropdownItem><a onClick={this.handleLogoutClick} href="#"></a></DropdownItem>
                    </Dropdown>
                  </div>
                )
                : (
                  <div styleName="nav--user">
                    <ul styleName="nav-links">
                      <li styleName="nav-item">
                        <Link styleName="nav-link" to="/signin"></Link>
                      </li>
                      <li styleName="nav-item">
                        <Link styleName="nav-link" to="/signup"></Link>
                      </li>
                    </ul>
                  </div>
                )
            }
          </div>
        </Container>
      </div>
    )
  }
}

export default Branding



