import React, { Component } from 'react'
import CSSModules from 'react-css-modules'
import isDescendant from '../../utils/dom/isDescendant'
const styles = require('./Dropdown.scss')

interface IProps {
  title: string | JSX.Element
  className?: string
  styleName?: string
  style?: 'blue' | 'dark'
}

interface IState {
  showMenu?: boolean
}

@CSSModules(styles, {
  allowMultiple: true
})
class Dropdown extends Component<IProps, IState> {

  dropdown: any

  constructor(props) {
    super(props)
    this.state = {
      showMenu: false
    }
    this.handleGlobalClick = this.handleGlobalClick.bind(this)
  }

  handleGlobalClick(e) {
    //  dom  dropdown 
    //  toggleDropdown 
    //  dropdown 
    if (!isDescendant(this.dropdown, e.target)) {
      this.setState({
        showMenu: false
      })
    } else {
      //  synthetic react  bug
      this.setState({
        showMenu: !this.state.showMenu
      })
    }
  }

  componentDidMount() {
    window.addEventListener('click', this.handleGlobalClick)
  }

  componentWillUnmount() {
    window.removeEventListener('click', this.handleGlobalClick)
  }

  render() {
    const { className, style } = this.props

    return (
      <div ref={ref => { this.dropdown = ref } } styleName="dropdown" className={className || ''}>
        <div styleName="dropdown-toggle">
          <span>{this.props.title}</span>
          <span styleName="dropdown-caret"/>
        </div>
        {
          this.state.showMenu && (
            <div styleName={ style === 'dark' ? 'dropdown-menu--dark' : 'dropdown-menu'}>
              <ul>
                {this.props.children}
              </ul>
            </div>
          )
        }
      </div>
    )
  }
}

export default Dropdown



