import * as schemas from '../data/schemas'
import Model from '../models/model'
import _ from 'lodash'
import * as helpers from '../helpers'

const fileModel = new Model(schemas.file)

// @req#loggedFileId
export default function logFile(req, res, next) {
  const file = req.file
  const hash = helpers.computeHash(file.buffer)
  const data = {
    filename: file.originalname,
    //  binData  BSON type
    content: file.buffer,
    mimetype: file.mimetype,
    size: file.size,
    encoding: file.encoding,
    hash
  }

  fileModel.findOne({ hash }).then(result => {
    // req.loggedFileId = result._id
    // TODO: 404 
    const err = new Error('File already exists!')
    next(err)
  }, notFoundError => {
    // TODO:  404
    // 
    fileModel
      .add(data)
      .then(result => {
        const loggedFileId = _.get(result, ['ops', 0, '_id'])
        if (!loggedFileId) {
          // TODO
          // throw new Error('File not Found!')
          const err = new Error('File not Found!')
          next(err)
        } else {
          req.loggedFileId = loggedFileId
          next()
        }
      })
      .catch(error => {
        next(error)
      })
  })
}



